import { Component, h, Prop, Element, Host } from '@stencil/core';

@Component({
  tag: 'ks-cart-tile',
  styleUrl: 'orderTile.css'
})
export class CartTile {
    @Element() root: HTMLElement;

    @Prop() name: string;
    @Prop() color: string;
    @Prop() logo: string;
    @Prop() price: string;

    render() {
        return <Host>
            <div class="header">
                <ks-img3 image={this.logo} alt={this.name} fit='contain' width={130} height={40} />
                {this.price && <div>{this.price}</div>}
            </div>

            <div class="content">
                <slot/>
            </div>
        </Host>;
    }
}